                    </symbol>
                </defs>
            </svg>
            <div class="container">
                <div class="checkout-addr">
                    <div class="page-title-normal">
                        <h2 class="page-title-h2"><span>check out</span></h2>
                    </div>
                    <!-- process step -->
                    <div class="check-step">
                        <ul>
                            <li class="cur">{{$t("address.confirmAddress")}}</li>
                            <li>{{$t("address.viewYourOrder")}}</li>
                            <li>{{$t("address.makePayment")}}</li>
                            <li>{{$t("address.orderConfirmation")}}</li>
                        </ul>
                    </div>

                    <!-- address list -->
                    <div class="page-title-normal checkout-title">
                        <h2><span>{{$t("address.shippingAddress")}}</span></h2>
                    </div>
                    <div class="addr-list-wrap">
                        <div class="addr-list">
                            <ul>
                                <li v-for="(item,index) in addressListFilter" :class="{'check':checkIndex ===index}" @click="checkIndex=index;selectedAddressId=item.addressId">
                                    <dl>
                                        <dt v-text="item.userName"></dt>
                                        <dd class="address" v-text="item.streetName"></dd>
                                        <dd class="tel" v-text="item.tel"></dd>
                                    </dl>
                                    <div class="addr-opration addr-del">
                                        <a href="javascript:;" class="addr-del-btn" @click="delAddressConfirm(item.addressId)">
                                          <svg class="icon icon-del"><use xlink:href="#icon-del"></use></svg>
                                        </a>
                                    </div>
                                    <div class="addr-opration addr-set-default">
                                        <a href="javascript:;" class="addr-set-default-btn" v-show="!item.isDefault" @click="setDefaultAddress(item.addressId)"><i>{{$t("address.setDefault")}}</i></a>
                                    </div>
                                    <div class="addr-opration addr-default"  v-show="item.isDefault" @click="setDefaultAddress(item.addressId)">{{$t("address.defaultAddress")}}</div>
                                </li>
                                <li class="addr-new">
                                    <div class="add-new-inner">
                                        <i class="icon-add">
                                            <svg class="icon icon-add"><use xlink:href="#icon-add"></use></svg>
                                          </i>
                                        <p>{{$t("address.addNewAddress")}}</p>
                                    </div>
                                </li>
                            </ul>
                        </div>
                        <div class="shipping-addr-more">
                            <a class="addr-more-btn up-down-btn" href="javascript:;" @click="expand" :class="{'open':limit>3}">
                            {{$t("address.more")}}
                            <i class="i-up-down">
                              <i class="i-up-down-l"></i>
                              <i class="i-up-down-r"></i>
                            </i>
                          </a>
                        </div>
                    </div>
                    <!-- shipping method-->
                    <div class="page-title-normal checkout-title">
                        <h2><span>{{$t("address.shippingMethod")}}</span></h2>
                    </div>
                    <div class="lemall-msg-info hidden">
                        <span>{{$t("address.msgInfo")}}</span>
                    </div>
                    <div class="shipping-method-wrap">
                        <div class="shipping-method">
                            <ul>
                                <li class="check">
                                    <div class="name">{{$t("address.standardShipping")}}</div>
                                    <div class="price">{{$t("address.free")}}</div>
                                    <div class="shipping-tips">
                                        <p>{{$t("address.tips")}}</p>
                                    </div>
                                </li>
                            </ul>
                        </div>
                    </div>
                    <div class="next-btn-wrap">
                        <router-link class="btn btn--m btn--red" :to="{path:'/orderConfirm',query:{'addressId':selectedAddressId}}">{{$t("address.next")}}</router-link>
                    </div>
                </div>
            </div>
        </div>
        <Modal :modalShow="isMdShow" @close="closeModel">
          <p slot="message">您是否确认要删除此地址？</p>
          <div slot="btnGroup">
            <a href="javascript:;" class="btn btn--m" @click="delAddress">确认</a>
            <a href="javascript:;" class="btn btn--m" @click="closeModel">取消</a>
          </div>
        </Modal>
        <Modal :modalShow="isMdShow2" @close="closeModel2=false">
          <p slot="message">地址列表至少需要一条数据，已无法继续删除！</p>
          <div slot="btnGroup">
            <a href="javascript:;" class="btn btn--m btn--red" @click="closeModel2=false">好的👌</a>
          </div>
        </Modal>
    </div>
</template>
<script>
import Modal from '@/components/modal'

export default {
    name: 'address-list',
    components: {Modal},
    data(){
      return{
        limit:3,
        checkIndex:0,
        isMdShow:false,
        isMdShow2:false,
        selectedAddressId:'',
        addressId:'',
        addressList:[]
      }
    },
    computed:{
      addressListFilter(){
        return this.addressList.slice(0,this.limit)
      }
    },
    mounted(){
      this.getAddressList()
    },
    methods:{
      getAddressList(){
        this.$axios.get("users/addressList").then(res =>{
          let data = res.data
          this.addressList = data.result
          if (data.result.length>0) {
            this.selectedAddressId = data.result[0].addressId
          }
        })
      },
      // 展开更多地址
      expand(){
        if (this.limit === 3) {
          this.limit = this.addressList.length
        }else{
          this.limit = 3
        }
      },
      // 设置默认地址
      setDefaultAddress(addressId){
        this.$axios.post("users/setDefaultAddress",{addressId:addressId}).then(res =>{
          let data = res.data
          if (data.status) {
            alert("设置默认地址失败！")
          }else{
            this.getAddressList()
          }
        })
      },
      // 打开删除地址框
      delAddressConfirm(addressId){
        if (this.addressList.length>1) {
          this.isMdShow = true
          this.addressId = addressId
        }else{
          this.isMdShow2 = true
        }
      },
      // 删除地址
      delAddress(){
        this.$axios.post('users/delAddress',{addressId:this.addressId}).then(res=>{
          let data = res.data
          if (data.status) {
            alert("删除地址失败！")
          }else{
            this.getAddressList()
            this.isMdShow = false
          }
        })
      },
      // 关闭模态框
      closeModel(){
        this.isMdShow = false
      }
    }
}
</script>
