  <div class="container">
    <div class="checkout-order">
      <div class="page-title-normal">
        <h2 class="page-title-h2"><span>{{$t("orderConfirm.checkOut")}}</span></h2>
      </div>
      <!-- process step -->
      <div class="check-step">
        <ul>
          <li class="cur">{{$t("address.confirmAddress")}}</li>
          <li class="cur">{{$t("address.viewYourOrder")}}</li>
          <li>{{$t("address.makePayment")}}</li>
          <li>{{$t("address.orderConfirmation")}}</li>
        </ul>
      </div>
      <!-- order list -->
      <div class="page-title-normal checkout-title">
        <h2><span>{{$t("orderConfirm.orderContent")}}</span></h2>
      </div>
      <div class="item-list-wrap confirm-item-list-wrap">
        <div class="cart-item order-item">
          <div class="cart-item-head">
            <ul>
              <li>{{$t("orderConfirm.orderContents")}}</li>
              <li>{{$t("cartList.price")}}</li>
              <li>{{$t("cartList.quantity")}}</li>
              <li>{{$t("cartList.subtotal")}}</li>
            </ul>
          </div>
          <ul class="cart-item-list">
            <li v-for="item in cartList" v-if="item.checked === '1'">
              <div class="cart-tab-1">
                <div class="cart-item-pic">
                	<img :src="item.productUrl" :alt="item.productName" :title="item.productName">
                </div>
                <div class="cart-item-title">
                  <div class="item-name" v-text="item.productName"></div>

                </div>
              </div>
              <div class="cart-tab-2">
                <div class="item-price">{{item.salePrice|currency('¥')}}</div>
              </div>
              <div class="cart-tab-3">
                <div class="item-quantity">
                  <div class="select-self">
                    <div class="select-self-area">
                      <span class="select-ipt" v-text="'x '+item.productNum"></span>
                    </div>
                  </div>
                  <div class="item-stock item-stock-no">{{$t("orderConfirm.inStock")}}</div>
                </div>
              </div>
              <div class="cart-tab-4">
                <div class="item-price-total">{{(item.salePrice*item.productNum)|currency('¥')}}</div>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <!-- Price count -->
      <div class="price-count-wrap">
        <div class="price-count">
          <ul>
            <li>
            	<!-- 总计 -->
              <span>{{$t("cartList.itemTotal")}}:</span>
              <span>{{subTotal|currency('¥')}}</span>
            </li>
            <li>
            	<!-- 配送费 -->
              <span>{{$t("orderConfirm.Shipping")}}:</span>
              <span>{{shipping|currency('¥')}}</span>
            </li>
            <li>
            	<!-- 折扣 -->
              <span>{{$t("orderConfirm.Discount")}}:</span>
              <span>{{discount|currency('¥')}}</span>
            </li>
            <li>
            	<!-- 扣税 -->
              <span>{{$t("orderConfirm.Tax")}}:</span>
              <span>{{tax|currency('¥')}}</span>
            </li>
            <li class="order-total-price">
            	<!-- 总价格 = 总计 + 配送费 - 折扣 + 扣税 -->
              <span>{{$t("orderConfirm.orderTotal")}}:</span>
              <span>{{orderTotal|currency('¥')}}</span>
            </li>
          </ul>
        </div>
      </div>

      <div class="order-foot-wrap">
        <div class="prev-btn-wrap">
          <router-link class="btn btn--m" to="/address">{{$t("orderConfirm.previous")}}</router-link>
        </div>
        <div class="next-btn-wrap">
          <button class="btn btn--m btn--red" @click="payMent">{{$t("orderConfirm.proceedToPayment")}}</button>
        </div>
      </div>
    </div>
  </div>
  </div>
</template>
<script>

export default {
    name: 'order-confirm',
    components: {
    },
    data(){
    	return {
    		subTotal:0,
    		shipping:100,
    		discount:200,
    		tax:400,
    		orderTotal:0,
    		cartList:[]
    	}
    },
    mounted() {
        this.getCartList()
    },

    methods:{
    	// 获取购物车列表
    	getCartList(){
    		this.$axios.get("users/cartList").then(res =>{
    			let data = res.data
    			this.cartList = data.result
    			this.cartList.forEach(item =>{
    				if(item.checked === '1'){
    					// 总金额 += 单价 * 数量
    					this.subTotal += item.salePrice*item.productNum
    				}
    			})
    			this.orderTotal = this.subTotal + this.shipping - this.discount + this.tax
    		})
    	},
    	// 支付
    	payMent(){
    		let _addressId = this.$route.query.addressId
    		this.$axios.post("users/payMent",{addressId:_addressId,orderTotal:this.orderTotal}).then(res=>{
    			let data = res.data
    			if (data.status) {
    				alert("生成订单失败！")
    			}else{
    				alert("生成订单成功，点击确认跳转")
    				this.$router.push({path:"/orderSuccess?orderId="+data.result.orderId})
    			}
    		})
    	}
    }
}
</script>

